  left: 25%;
  position: absolute;
  margin-top: 30%;
  -webkit-animation: bouncy_scuba_diver 3s infinite alternate;
          animation: bouncy_scuba_diver 3s infinite alternate;
}

@-webkit-keyframes bouncy_scuba_diver {
  from {
    -webkit-transform: translateY(5px) rotate(-1deg);
            transform: translateY(5px) rotate(-1deg);
  }
  to {
    -webkit-transform: translateY(-25px) rotate(1deg);
            transform: translateY(-25px) rotate(1deg);
  }
}

@keyframes bouncy_scuba_diver {
  from {
    -webkit-transform: translateY(5px) rotate(-1deg);
            transform: translateY(5px) rotate(-1deg);
  }
  to {
    -webkit-transform: translateY(-25px) rotate(1deg);
            transform: translateY(-25px) rotate(1deg);
  }
}

