    0%{
        left: -300px;
        -webkit-transform: scaleX(-1);
                transform: scaleX(-1);
    }
    50% {
        left: calc(100vw + 300px);
        -webkit-transform: scaleX(-1);
                transform: scaleX(-1);
    }
    51%{
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
    100% {
        left: -300px;
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
}

